<style scoped>
   a {
    color:#000000;
  }

</style>
<template>
  <div class="uploadalg">

<el-row>
  <el-col :span="6"><div class="grid-content" @click="changenewshow()" style="text-align:left">
      
          <i class="el-icon-circle-plus" v-show="!newshow">
          </i>
          <i class="el-icon-remove" v-show="newshow">
        </i> <a>上传算法包</a>
    </div></el-col>
  <el-col :span="18"><div class="grid-content" ></div></el-col>
</el-row>
  <div v-show="newshow">
   <el-row :gutter="20">
    <el-col :span="12">
     <el-row>
       <el-col :span="12">
         上传算法包
       </el-col>
       <el-col :span="12">
         <div @change='changename'>
            <input type="file"  class="pull-left btn btn-warning" id="alg_jar" name="alg_jar" accept=".jar" >
         </div>
       </el-col>
       <br/>
        <br/> <br/> <br/> <br/> <br/>
      <el-col :span="12">
         算法包名称
       </el-col>
       <el-col :span="12">

       <el-input
        placeholder="请填入算法包名称"
        v-model="filename"
        clearable>
        </el-input>

       </el-col>
     </el-row>
    </el-col>
    <el-col :span="12">
         <div class="form-group">
                  <label for="username" class="control-label"></label>
                  <div>
                    <p align="left" style="color:gray">提示：
                    </p>
                    <p align="left" style="color:gray">算法包格式： 算法包只支持jar包
                    </p>
                    <p align="left" style="color:gray">输入：输入字段为整个文件或表的全字段
                    </p>
                    <p align="left" style="color:gray">输出：输出字段也为整个文件或表的全字段
                    </p>
                    <p align="left" style="color:gray">清洗标识：输出数据文件或表中增加一列标识
                    </p>
                    <p align="left" style="color:gray">flag:1为清洗过，0为未清洗<br/>
                    </p>

                  </div>
                </div>
    </el-col>
         <el-button @click="upload" class="btn btn-success" >提交</el-button>
          <el-button @click='clear' class="btn btn-warning">重置</el-button>
         <el-button type="info">收起</el-button>
   </el-row>
  </div>
</div>
</template>
<script>

  export default {
    data() {
      return {
       newshow:false,
      filename: '', //导入文件名
      jarname:'',
      }
    },
    computed: {
   
  },
    created: function () {
      var vm = this;
    

     


    }, 
    methods: {
      changenewshow(){
      var vm = this;
      vm.newshow = !vm.newshow;

       },
       clear: function() {
        var vm = this;
        vm.filename = '';
        vm.a=false;
        var obj = document.getElementById('alg_jar');
        obj.outerHTML = obj.outerHTML;
      },
      changename: function() {
        var vm = this;
        vm.a =true;
        var path = $("#alg_jar").val();
        var pathstr = path.split("\\");
        vm.filename = pathstr[pathstr.length - 1];
      },
       upload: function(id) {
        var vm = this;
//        var url = vm.url + "api/algorithms/upload/";
        var url = "http://localhost:8080/dataclean/servlet/DataCleanServlet";
        var formData = new FormData();
        formData.append("jar", document.getElementById("alg_jar").files[0]);
        vm.jarname=document.getElementById("alg_jar").files[0].name;
        var path = $("#alg_jar").val();
        var pathstr = path.split("\\");
        var filetypes =[".jar"];
        var fileend = vm.filename.substring(vm.filename.indexOf("."));
        var isnext = false;
        var filenoend = vm.filename.substring(0,vm.filename.indexOf("."))
        vm.a=true;
        if (path == '') {
          alert("请选择文件");
          return;
        }
        console.log("filename:" + vm.filename);
        if (vm.filename == '') {
          alert("请填写完整信息");
          return;
        }

        var testjar=pathstr[pathstr.length-1].split('.');
        // if(testjar[testjar.length-1]!="jar"){
        //   alert("请选择jar格式的文件");
        //   return;
        // }
        if (/^[\u4e00-\u9fa5]+$/i.test(filenoend)) {

          alert("算法包名称请不要使用中文");
          return;
        }
        formData.append("name", vm.filename);
        formData.append("userid", vm.userid);
        //   console.log("path"+path);
        //              $.ajax({
        //                  url: url,
        //                  type: 'POST',
        //                  cache: false,
        //                  data: formData,
        //                  processData: false,
        //                  contentType: false,
        //                  success: function(data) {
        //
        //                  },
        //                  error: function(data) {}
        //              });
        //

        $.ajax({
          url: url,
          type: 'POST',
          data: formData,
          /**
           * 必须false才会避开jQuery对 formdata 的默认处理
           * XMLHttpRequest会对 formdata 进行正确的处理
           */
          processData: false,
          /**
           *必须false才会自动加上正确的Content-Type
           */
          contentType: false,
          dataType: 'json',

          success: function(data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
            //    console.log(Obj);

            if (Obj.status == 0) {
              // console.log(data);
              alert("上传成功");
              vm.clear();
            } else {
              alert("上传失败" + ":" + Obj.info);
             
            }
            vm.clear();
          },
          error: function(data) {
          //  alert("上传出错");
          alert("上传成功");
               vm.submitjar();
            console.log(data); //将json对象转成json字符串。
            vm.clear();
          }
        });
      },
      submitjar(){
           var vm = this;
        var url = "http://localhost:8080/dataclean/servlet/addjar";
  
         var data_tmp = {
           "jarfile":vm.jarname,
           "jarname":vm.filename,
          };
           $.ajax({
          url: url,
          type: "POST",
          async:false,
          dataType: 'json',
          data: {
            'data_json': JSON.stringify(data_tmp)
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
            vm.fresh();
          },
           error: function (data) {
            console.log(data);
          }
        });
      },
       fresh() {
      this.$emit('fresh'); //主动触发upup方法，'hehe'为向父组件传递的数据
    },
    }
  }
</script>



