<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.algmanage .el-table th{
  text-align: center
}


</style>
<template>
  <div class="algmanage">
  <el-container>
  <el-header style="background-image: url('./static/img/yunjian22.png'); background-size: cover; background-position: top center;">流数据清洗工具</el-header>
  <el-container>
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)"  > <nevigation></nevigation></el-aside>
    <el-main>
       <h3 style="text-align:right">
              <i class="el-icon-info">
              </i>
              算法库管理
            </h3>
      <uploadalg @fresh="upadatenewalg"></uploadalg>
      <newalg @fresh="getAlg" ref="profile"></newalg>
   <el-row :gutter="10">
    <el-col :span="6"><div class="grid-content" style="text-align:left">
          <i class="el-icon-date" >
        </i> <a>算法列表</a>
    </div></el-col>
  <el-col :span="18"><div class="grid-content" ></div></el-col>
  <el-row>
    <el-col :span="24">
        <el-table
    :data="tableData4"
    style="width: 100%"
    max-height="500">
    <el-table-column
      fixed
      prop="algid"
      label="算法ID"
      width="150">
    </el-table-column>
    <el-table-column
      prop="algtype"
      label="算法类型"
      width="200">
    </el-table-column>
    <el-table-column
      prop="algname"
      label="算法名称"
      width="200">
    </el-table-column>
    <el-table-column
      prop="jarname"
      label="算法包"
      width="200">
    </el-table-column>
     <el-table-column
      prop="algclass"
      label="算法的类名"
      width="200">
    </el-table-column>
     <el-table-column
      prop="parameter"
      label="输入参数"
      width="200">
    </el-table-column>
    <el-table-column
      prop="algdesc"
      label="算法描述"
      width="120">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="200">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="deleteconfirm(scope.$index, scope.row,tableData4)"
          type="text"
          size="small">
          移除
        </el-button>
        <el-button
          @click.native.prevent="deleteRow(scope.$index, tableData4)"
          type="text"
          size="small">
          修改
        </el-button>
      </template>
    </el-table-column>
  </el-table>
    </el-col>
  </el-row>
</el-row>
    </el-main>
  </el-container>
</el-container>
  </div>
</template>
<script>
  import nevigation from "./sub/nevigation.vue";
  import newalg from "./sub/newalg.vue";
  import uploadalg from "./sub/uploadalg.vue";
  import uploadAlgo from "./sub/uploadAlgo.vue";
  export default {
    components: {
      nevigation,
      newalg,
      uploadalg,
      uploadAlgo,
    },
    data() {
      return {
         tableData4: [],
      }
    },
    created: function () {
      var vm = this;
      vm.getAlg();
  
    },

    methods: {
     upadatenewalg(){
     this.$refs.profile.getjar()
     },
     getAlg(){
        var vm = this;
        vm.tableData4 = [];
         var url = "http://localhost:8080/dataclean/servlet/Alg";
        var data_tmp = {
        };
       $.ajax({
          url: url,
          type: "POST",
          dataType: 'json',
          data: {
            'data_json': JSON.stringify(data_tmp)
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
            if(Obj.length>0){
              for (var i = 0; i < Obj.length; i++) {
                vm.tableData4.push({
                  "algid":Obj[i].algid,
                  "algtype":Obj[i].algtype,
                  "algname":Obj[i].algname,
                  "jarname":Obj[i].jarname,
                  "algclass":Obj[i].algclass,
                  "parameter":Obj[i].parameter,
                  "algdesc":Obj[i].algdesc,
                  
                });
                
              }}
              else{
                console.log("无数据");
              }
          },
           error: function (data) {
            console.log(data);
          }
        });
     },
   deleteconfirm:function(index,row,tableData4){
            var vm=this;
            var r=confirm("请确认是否删除");
            if (r==true)
            {
              vm.deletealg(index,row,tableData4);
            }
            else
            {
              return;
            }
          },
      deletealg(index,row,tableData4){
         var vm = this;
         var url = "http://localhost:8080/dataclean/servlet/deletealg";
        var data_tmp = {
        "algname":row.algname,
        };
       $.ajax({
          url: url,
          type: "POST",
          dataType: 'json',
          data: {
            'data_json': JSON.stringify(data_tmp)
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
            vm.getAlg();
                
      
          },
           error: function (data) {
            console.log(data);
          }
        });
      }
    }

  }

</script>



